import Vue from 'vue'
import VueClipboard2 from 'vue-clipboard2'
import App from './App.vue'
import store from './store'
import vuetify from './plugins/vuetify';

Vue.config.productionTip = false

Vue.use(VueClipboard2)

new Vue({
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')
